import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function reportedCurrency(_tableName: string) {
  const title = "Reported Currency"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["reportedCurrency"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The currency used in the report.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyBalanceStatement?.data?.reportedCurrency}
          queryResult={info.row.original.financialData.companyBalanceStatement}
        >
          {info.row.original.financialData.companyBalanceStatement?.data?.reportedCurrency}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { reportedCurrency }
